"use client";

import Image from "next/image";
import { BentoGrid, BentoCard } from "@/components/magicui/bento-grid"; // adjust the path as needed

interface HuboHeadingProps {
  text: string;
}

export default function HuboHeading({ text }: HuboHeadingProps) {
  return (
    <section className="w-[90vw] bg-transparent text-white py-10 flex flex-col gap-10 mx-auto">
      
      {/* Top Heading */}
      <div className="text-center px-4">
        <h1 className="text-4xl md:text-5xl font-extrabold leading-tight">
          {text}
        </h1>
      </div>

      {/* Image Grid using BentoGrid */}
      <BentoGrid className="mx-auto">
        <BentoCard
          className="bg-muted"
          title="AI Visual 1"
          description="Explore intelligent design"
          header={
            <Image
              src="/img/ai.jpeg"
              alt="AI Image 1"
              width={500}
              height={300}
              className="w-full h-48 object-cover rounded-md"
            />
          }
        />
        <BentoCard
          className="bg-muted"
          title="AI Visual 2"
          description="Smart and aesthetic"
          header={
            <Image
              src="/img/ai1.jpg"
              alt="AI Image 2"
              width={500}
              height={300}
              className="w-full h-48 object-cover rounded-md"
            />
          }
        />
        <BentoCard
          className="bg-muted"
          title="AI Visual 3"
          description="Modern creativity"
          header={
            <Image
              src="/img/ai2.jpg"
              alt="AI Image 3"
              width={500}
              height={300}
              className="w-full h-48 object-cover rounded-md"
            />
          }
        />
        <BentoCard
          className="bg-muted"
          title="AI Visual 4"
          description="Future of visuals"
          header={
            <Image
              src="/img/ai3.jpg"
              alt="AI Image 4"
              width={500}
              height={300}
              className="w-full h-48 object-cover rounded-md"
            />
          }
        />
      </BentoGrid>
    </section>
  );
}
